import React from "react";
import Card from "../components/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import pic from "../components/images/pic.png";
import picc from "../components/images/picc.png";
import piccc from "../components/images/piccc.png";

class Carousel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          title: "Digital",
          subTitle: "capture the moments",
          imgSrc: pic,
          link: "https://hanyueshi.myportfolio.com/",
          selected: false,
        },
        {
          id: 1,
          title: "Portrait",
          subTitle: "memories in a moment",
          imgSrc: picc,
          link: "https://hanyueshi.myportfolio.com/portrait",
          selected: false,
        },
        {
          id: 2,
          title: "Film",
          subTitle: "think more before shooting",
          imgSrc: piccc,
          link: "https://hanyueshi.myportfolio.com/15ffc5936a273c",
          selected: false,
        },
      ],
    };
  }

  handleCardClick = (id, card) => {
    console.log(id);

    let items = [...this.state.items];

    items[id].selected = items[id].selected ? false : true;

    items.forEach((item) => {
      if (item.id !== id) {
        item.selected = false;
      }
    });

    this.setState({
      items,
    });
  };

  makeItems = (items) => {
    return items.map((item) => {
      return (
        <Card
          item={item}
          click={(e) => this.handleCardClick(item.id, e)}
          key={item.id}
        />
      );
    });
  };

  render() {
    return (
      <Container fluid={true}>
        <Row className="justify-content-around">
          {this.makeItems(this.state.items)}
        </Row>
      </Container>
    );
  }
}

export default Carousel;
